import { Component, OnInit, ViewChild , HostBinding } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../services/Authentication.service';

import { Observable } from 'rxjs';

import { Plugins } from '@capacitor/core';
const { Geolocation } = Plugins;

import { GoogleMapComponent } from '../../components/google-map/google-map.component';



@Component({
  selector: 'app-ubicaciones',
  templateUrl: './ubicaciones.page.html',
  styleUrls: [
    './styles/ubicaciones.page.scss',
    './styles/ubicaciones.shell.scss',
    './styles/ubicaciones.page.ios.scss'
  ]
})
export class UbicacionesPage implements OnInit {
  googleMapsUrl: string;
  WazeUrl: string;

  countries: Array<string>;
  listing: Observable<any>;

select1: any;
  constructor(private route: ActivatedRoute, private authenticationService: AuthenticationService ) {


  }


  ngOnInit(): void {

    this.countries = [
      'Guatemala',
      'Escuintla',
      'Jutiapa',
      'Izabal',
      'Suchitepequez',
      'Zacapa',
      'Solola',
      'Alta Verapaz',
      'Baja Verapaz',
      'Chimaltenango',
      'Chiquimula',
      'El Progreso',
      'Jalapa',
      'Petén',
      'Quetzaltengango',
      'Quiché',
      'Retalhuleu',
      'Sacatepequez',
      'San Marcos',
      'Santa Rosa'
    ];

this.select1 = 'Guatemala';

  }



  abregoogle(item,type): void {

    this.googleMapsUrl = "https://www.google.com/maps/search/?api=1&query="+item.ubicax+ "+"+item.ubicay;
    this.WazeUrl = "https://waze.com/ul?ll=" + item.ubicax + "," + item.ubicay + "&z=10";

    if (type === 'google') {
      // window.open(this.googleMapsUrl);
      window.open(encodeURI(this.googleMapsUrl), '_system', 'location=no');
    } else {
      window.open(encodeURI(this.WazeUrl), '_system', 'location=no');
    }



  }
  ionViewDidEnter(): void {
//  this.id = this.activatedRoute.snapshot.params["id"];

    this.authenticationService.getData('./assets/sample-data/sucursal.json').subscribe(response => {
      console.log(response);
      this.listing = response;
    });





  }
}
